.header {
    height: 60px;
    padding-left: var(--mantine-spacing-md);
    padding-right: var(--mantine-spacing-md);
    border-bottom: 1px solid var(--mantine-color-gray-3);
  }
    [data-mantine-color-scheme='dark'] .header {
    border-bottom: 1px solid var(--mantine-color-dark-4);
}
  
  .image {
    display: flex;
    align-items: center;
    width: 3vw;
    height: 5vh;
  }
  
  @media (max-width: 48em) {
  
  .image {
      height: 42px;
      width: 100%
  }
    }
  
  @media (hover: hover) {
    .image:hover {
      background-color: var(--mantine-color-gray-0);
    }
      [data-mantine-color-scheme='dark'] .image:hover {
      background-color: var(--mantine-color-dark-6);
    }
}
  
  @media (hover: none) {
    .image:active {
      background-color: var(--mantine-color-gray-0);
    }
      [data-mantine-color-scheme='dark'] .image:active {
      background-color: var(--mantine-color-dark-6);
    }
}
  
  .subLink {
    width: 100%;
    padding: var(--mantine-spacing-xs) var(--mantine-spacing-md);
    border-radius: var(--mantine-radius-md);
  }
  
  @media (hover: hover) {
    .subLink:hover {
      background-color: var(--mantine-color-gray-0);
    }
      [data-mantine-color-scheme='dark'] .subLink:hover {
      background-color: var(--mantine-color-dark-7);
    }
}
  
  @media (hover: none) {
    .subLink:active {
      background-color: var(--mantine-color-gray-0);
    }
      [data-mantine-color-scheme='dark'] .subLink:active {
      background-color: var(--mantine-color-dark-7);
    }
}
  
  .dropdownFooter {
    background-color: var(--mantine-color-gray-0);
  }
  
  [data-mantine-color-scheme='dark'] .dropdownFooter {
    background-color: var(--mantine-color-dark-7);
}
  
  .dropdownFooter {
    margin: calc(var(--mantine-spacing-md) * -1);
    margin-top: var(--mantine-spacing-sm);
    padding: var(--mantine-spacing-md) calc(var(--mantine-spacing-md) * 2);
    padding-bottom: var(--mantine-spacing-xl);
    border-top: 1px solid var(--mantine-color-gray-1);
}
  
  [data-mantine-color-scheme='dark'] .dropdownFooter {
    border-top: 1px solid var(--mantine-color-dark-5);
}